import Vue from "vue";
import Vuex from "vuex";
import persist from "vuex-persist";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    list: [
      {
        user: "王小虎",
        pwd: "15565801176",
        region: "启用",
      },
    ],
  },
  mutations: {
    bj(state, val) {
      state.list[val.index] = val.val;
    },
    add(state, val) {
      console.log(val);
      state.list.push(val);
    },
    sc(state, val) {
      state.list.forEach((res, index) => {
        if (val == index) {
          state.list.splice(index, 1);
        }
      });
    },
  },
  actions: {},
  modules: {},
  plugins: [
    new persist({
      storage: window.localStorage,
    }).plugin,
  ],
});
